
import XDialog from 'vux/src/components/x-dialog/index.vue'

import XBag from "@/pages/bag/bag";
import XShop from "@/pages/shop/shop";
import { clearTimeout } from 'timers';

export default {
components: {
XDialog,
XBag,
XShop
},
methods: {
option_animation() {
clearTimeout()
setTimeout(() => {
this.option_show_flag = false;
}, 2000);
this.option_show_flag = true;
}
},
data() {
return {
isLogin: false,
user: {
headImg: "",
exp: 200,
money: 120,
title: "种植大师",
plant: {
name: "兰花",
plantUrl: "/static/img/1/3.png"
}
},
showShop: false,
showBag: false,
option_show_flag: false
};
}
};



