
import XHead from "@/components/header/header";
import XContent from "./xcontent";

import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
XHead,
Actionsheet,
XContent
},
computed: {
approve: function() {
return (
this.parentUrl + (this.approveFlag ? "approve.png" : "Rapprove.png")
);
},
oppose: function() {
return this.parentUrl + (this.opposeFlag ? "oppose.png" : "Roppose.png");
},
collect: function() {
return (
this.parentUrl + (this.collectFlag ? "collect.png" : "Rcollect.png")
);
}
},
data() {
return {
title: "水仙",
count: 1600,
menus: {},
parentUrl: "/static/img/",
showMenus: false,
approveFlag: true,
opposeFlag: true,
collectFlag: true
};
},
methods: {
}
};



