
import XHead from "@/components/header/header";
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
mounted(){
this.$router.push({path: '/showItems'}),
this.flag=true
},
components: {
Tabbar,
TabbarItem,
XHead
},
data(){
return{
title: "词条",
flag: false,
isLogin: true
}
},
methods:{
changeTitleToItem(){this.title="词条", this.flag=true},
changeTitleToPotted(){
this.title="我的盆栽"
this.flag=false
this.$router.push({name: this.isLogin?'pottedPlant':'login', params: {back: '/pottedPlant'}})
},
changeTitleToAboutMe(){this.title="个人", this.flag=true}
}
};



